.page-hero {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.hero-content {
    background-image: url("img/hero.webp");
    background-size: cover;
    background-position-y: 75%;
    position: relative;
    color: #ffffff;
    flex-grow: 1;
}

.hero-content::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.25);
    z-index: 1;
}

.hero-wrapper {
    position: absolute;
    z-index: 2;
    top: 50%;
    transform: translateY(-50%);
}

.hero-credits {
    position: absolute;
    z-index: 2;
    bottom: 0.5rem;
    right: 0.5rem;
    padding: 0.25rem 0.5rem;
    border-radius: 5px;
    background-color: #ffffff;
    opacity: 0.25;
    color: #222222;
    font-size: 0.75rem;
}

.header {
    background-color: #eeeeee;
    padding: 1rem 0;
    color: #000000;
}

.header-wrapper {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
}

.header-image {
    height: 10rem;
    width: 10rem;
    background-image: url("./img/me.webp");
    background-size: cover;
    border-radius: 50%;
}
